.photo {
    width: 80px;
}
.paginatorContainer {
    display: flex;
    justify-content: center;
}
.usersContainer{
    margin-top: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 10px 10px;
    grid-template-areas:
    ". . . ."
    ". . . ."
    ". . . .";
    justify-items: center;
}
